.project {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 56px 0px 0px 0px;
    /* background-color: rgb(185, 185, 185); */
    height: 100vh;
    position: relative;
    animation-name: slidein;
    animation-duration: 1.5s;
}

@keyframes slidein {
    from {
        right: -500px;
    }
    to {
        right: 0px;
    }
}

.project__header {
    font-size: 24px;
    font-weight: 700;
    position: absolute;
    top: 10vh;
    left: 10vw;
}

.project__header:after {
    content: "";
    position: absolute;
    bottom: -20px;
    height: 3px;
    background-color: #DEDEDE;
    width: 28px;
    left: 0;
}

.project__projects {
    /* width: 40vw; */
    display: grid;
    grid-template-columns: 400px 400px;
    grid-template-rows: 400px 400px;
    grid-gap: 15px;
    position: relative;
    bottom: 28px;
}

.project__container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 400px;
    overflow: hidden;
    background-color: rgb(39, 39, 39, 0.575);
    position: relative;
}

.project__container1:hover {
    background-color: rgba(39, 39, 39, 1);
}

.project__container1:hover .project__title,
.project__container1:hover .project__description {
    display: block;
}

.project__container2 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 400px;
    overflow: hidden;
    background-color: rgb(39, 39, 39, 1);
    position: relative;
}

.project__container2:hover {
    background-color: rgba(39, 39, 39, 0.575);
}

.project__container2:hover .project__title,
.project__container2:hover .project__description {
    display: block;
}

.project__title {
    position: absolute;
    color: #ffffff;
    z-index: 1;
    font-size: 40px;
    font-weight: 700;
    /* width: 400px;
    height: 400px; */
    display: none;
}

.project__description {
    position: absolute;
    bottom: 140px;
    color: #ffffff;
    z-index: 1;
    font-size: 20px;
    font-weight: 700;
    display: none;
}

.project__elbows--img {
    width: 500px;
    position: relative;
    left: 15px;
}

.project__justcamp--img {
    width: 250px;
}

.project__rockinhood--img {
    width: 250px;
}

.project__flash--img {
    width: 250px;
    filter: brightness(0%);
}