.education {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 0px 0px 0px;
    height: 100vh;
    /* background-color: rgb(185, 185, 185); */
    position: relative;
    animation-name: slidein;
    animation-duration: 1.5s;
}

@keyframes slidein {
    from {
        right: -500px;
    }
    to {
        right: 0px;
    }
}

.education__header {
    font-size: 24px;
    font-weight: 700;
    position: absolute;
    top: 10vh;
    left: 10vw;
    /* right: 800px; */
}

.education__header:after {
    content: "";
    position: absolute;
    bottom: -20px;
    height: 3px;
    background-color: #DEDEDE;
    width: 28px;
    left: 0;
}

.education__conatiner {
    margin: 100px 0px 100px 0px;
    display: flex;
}

.education__education {
    height: 300px;
    width: 310px;
    background-color: #ffffff;
    margin: 0px 50px 0px 50px;
    padding: 40px;
    border: 1px solid black;
    box-shadow: 0px 0px 0px 1px #000000;
    transition: all 0.3s linear;
}

.education__education:hover {
    box-shadow: 0px 0px 0px 10px #000000;
    background-color: rgb(150, 150, 150);
    color: white;
}

.education__education:hover .education__type,
.education__education:hover .education__description {
    color: black;
}

.education__year {
    font-size: 24px;
    /* font-weight: 700; */
}

.education__type {
    font-size: 20px;
    font-weight: 700;
    color: rgb(150, 150, 150);
    margin: 50px 0px 0px 0px;
    transition: all 0.3s linear;
}

.education__name {
    margin: 10px 0px 60px 0px;
}

.education__description {
    margin: 0px 0px 0px 0px;
    color: rgb(150, 150, 150)
}
