.skills {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 56px 0px 0px 0px;
    height: 100vh;
    position: relative;
    /* background-color: rgb(185, 185, 185); */
    padding-top: 30px;
    animation-name: slidein;
    animation-duration: 1.5s;
}

@keyframes slidein {
    from {
        right: -500px;
    }
    to {
        right: 0px;
    }
}

.skills__header {
    font-size: 24px;
    font-weight: 700;
    position: absolute;
    top: 10vh;
    left: 10vw;
}

.skills__header:after {
    content: "";
    position: absolute;
    bottom: -20px;
    height: 3px;
    background-color: #DEDEDE;
    width: 28px;
    left: 0;
}

.skills__container {
    /* margin: 100px 0px 100px 0px; */
    display: grid;
    grid-template-columns: 250px 250px 250px 250px 250px;
    grid-gap: 40px;
    position: relative;
    bottom: 36px;
}

.skills__skill {
    width: 150px;
}

.skill__icon {
    font-size: 150px;
}